<template>
<div>
    <no-project v-if="!projectList.length"></no-project>
    <project-index :projectlist="projectList"  v-else></project-index>

</div>
</template>

<script>
import ProjectIndex from '@components/ethereum/projectIndex.vue';
import NoProject from '@components/ethereum/noProject.vue';
import api from '@/api';

export default {
  name: 'project',
  components: {
    ProjectIndex,
    NoProject,
  },
  data() {
    return {
      projectList: [],
    };
  },
  methods: {
    get_projects() {
      api.projectApi.get_projects().then((result) => {
        if (result && result.success) {
          this.projectList = result.msg;
        }
      });
    },
  },
  mounted() {
    this.get_projects();
  },


};
</script>

