<template>
  <ConfirmLayer v-if="isConfirm" @closeLayer="cancelCreate">
    <span slot="confirmtitle">Create new project</span>
    <div  slot="confirmcontent">
      <div>
        <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">Name<span
          class="text-pink">*</span></label></div>
        <input
          class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm"
          type="text" name="name" v-model="project_name">
        <div class="flex justify-end items-end flex-wrap mt-6 sm:flex-no-wrap">
          <button
            class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border border-gray-400 text-gray-600 shadow-none hover:bg-gray-200 active:bg-gray-300 mr-4" @click="cancelCreate">
            Cancel
          </button>
          <button
