          @click="submitCreate">Create
          </button>
        </div>
      </div>
    </div>
  </ConfirmLayer>
</template>

<script>
import mixin from '@mixin';
import ConfirmLayer from '@components/ConfirmLayer.vue';
import layer from '@/utils/layer';
import api from '@/api';

export default {
  name: 'createNew',
  mixins: [mixin],
  data() {
    return {
      project_name: '',
    };
  },
  components: {
    ConfirmLayer,
  },
  methods: {
    cancelCreate() {
      this.removeConfirm();
    },
    addProject() {
      this.addConfirm();
    },
    submitCreate() {
      const obj = {
        project_name: this.project_name,
        service_id: 1,
      };
      if (!this.project_name.length) {

      } else {
        api.projectApi.create_project(obj).then((result) => {
          if (!result.success) {
            layer.tip(result.msg, 'error');
          } else {
            layer.tip('Create new project successfully !');
            setTimeout(() => {
              this.cancelCreate();
              location.reload();
            }, 1000);
          }
        });
      }
    },
  },
  mounted() {
  },
};
</script>

